export default {
	data() {
		return {
			lunbo: [],
			tjpic: [],
			top: [],
			num: ""
		}
	},
	onLoad() {
		uni.request({
			url: 'http://musicapi.leanapp.cn/personalized',
			success: (res) => {
				this.tjpic = res.data.result
			}
		})
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		//歌单详情
		part(e) {
			uni.navigateTo({
				url: '../songs/songs?id=' + e.id + '&alls=' + e.trackCount + ''
			})
		}
	}
}
